<template>
  <div>
    <div class="containerBox">
      <!-- 页面标题 -->
      <div class="pageTopic">
        <span class="topicName">治疗方案设置</span>
      </div>

      <div class="formBox">
        <el-form ref="treatmentForm" :rules="rules" :model="treatmentParams" label-width="80px">
          <el-row>
            <el-col :span="12">
              <el-form-item label="预设处方">
                <el-select v-model="treatmentParams.presetPsId" placeholder="请选择处方" @change="selectPs">
                  <el-option v-for="item in psList" :key="item.psId" :label="item.psName" :value="item.psId" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="阳极区" prop="anode">
                <el-input v-model="treatmentParams.anode" placeholder="请输入阳极区"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="阴极区" prop="cathode" label-width="120px">
                <el-input v-model="treatmentParams.cathode" placeholder="请输入阴极区"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="电流" prop="current">
                <el-input v-model.number="treatmentParams.current" placeholder="请输入电流">
                  <template slot="append">微安/μA</template>
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="时长" prop="duration" label-width="120px">
                <el-input v-model.number="treatmentParams.duration" placeholder="请输入时长">
                  <template slot="append">分钟/min</template>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="12">
              <el-form-item label="刺激日期" prop="">
<!--                <el-radio-group v-model="cycleMode">
                  <el-radio :label="1">每天</el-radio>
                  <el-radio :label="2">间隔 <el-input-number size="small" v-model="customCycle" :min="1" :max="99"/> 天</el-radio>
                </el-radio-group-->
                <el-date-picker type="dates" v-model="treatmentParams.cycle" placeholder="选择一个或多个日期"></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="刺激时间区间" prop="timeInterval" label-width="120px">
                <el-time-picker is-range value-format="HH:mm:ss" v-model="treatmentParams.timeInterval" range-separator="至" start-placeholder="开始时间" end-placeholder="结束时间" />
              </el-form-item>
            </el-col>
          </el-row>

          <el-divider></el-divider>
          <el-form-item>
            <el-button type="primary" @click="save">保存</el-button>
            <el-button @click="back">返回</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import AppServerInf from "@/common/serverinf";

export default {
  data() {
    return {
      treatmentParams: {
        caseId: '',
        presetPsId: '',
        anode: '',
        cathode: '',
        current: '',
        duration: '',
        cycle: '',
        timeInterval: [],
      },
      psList: [],

      //timeInterval: [],

      //cycleMode: '',
      // customCycle: 1,

      rules:{
        anode: [{ required: true, message: '请输入阳极区', trigger: 'blur'}],
        cathode: [{ required: true, message: '请输入阴极区', trigger: 'blur'}],
        current: [{ required: true, type:'number', message: '请输入电流', trigger: 'blur'}],
        duration: [{ required: true, type:'number', message: '请输入时长', trigger: 'blur'}],
        timeInterval: [{ required: true, type:'array', message: '请输入刺激时间区间', trigger: 'change'}],
      },
    }
  },
  created() {
    const caseId = this.$route.params.caseId
    this.initTreatmentData(caseId)
    this.psList = this.$db.psList()
  },
  methods: {
    initTreatmentData(caseId){
      let tm = this.$db.newestTreatmentFromCase(caseId)
      if(tm) {
        this.treatmentParams = Object.assign(tm, {timeInterval: [tm.beginInterval, tm.endInterval]})
        // if(tm.cycle > 1) {
        //   this.cycleMode = 2
        //   this.customCycle = tm.cycle - 1
        // }
        //
        //this.treatmentParams.timeInterval = [tm.beginInterval, tm.endInterval]
      } else {
        this.treatmentParams.caseId = caseId
      }
    },
    selectPs(psId) {
      let ps = this.$db.ps(psId)
      this.treatmentParams.presetPsId = psId
      this.treatmentParams.anode = ps.psAnode
      this.treatmentParams.cathode = ps.psCathode
      this.treatmentParams.current = ps.psCurrent
      this.treatmentParams.duration = ps.psDuration
      //
      this.$forceUpdate()
    },

    save() {
      this.$refs['treatmentForm'].validate(valid=>{
        if (!valid) {
          return false
        }
        //
        // if(this.cycleMode === 1) {
        //   this.treatmentParams.cycle = 1
        // } else if(this.cycleMode > 1) {
        //   this.treatmentParams.cycle = this.customCycle + 1
        // }
        //
        this.treatmentParams.beginInterval = this.treatmentParams.timeInterval[0]
        this.treatmentParams.endInterval = this.treatmentParams.timeInterval[1]
        delete this.treatmentParams.timeInterval
        //
        this.$db.saveTreatment(this.treatmentParams)
        this.$modal.alert("保存成功")
        //
        this.back()
      })
    }
  }
}
</script>
<style>
</style>



